@import url(./main.scss);

$headerFont:white;
$HBC:black;
$headerAC:white;
$realsmallfns:10px;
$smallfns1:12px;
$smallfns2:14px;
$standardfns:16px;
$subtitle:20px;
$maintitle:24px;

.header_outer {
    width: 100%;
    height: 125px;
    background-color: $HBC;
    color: $headerFont;
    position: relative;

    .header {
        width: 1245px;
        height: 75px;
        margin: 0 auto;
        position: relative;

        .logo {
            display: block;
            width: 174px;
            height: 47px;
            // margin: 0 auto;
            // padding-top: 19px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 19px;
        }

        .gnb {
            float: right;
            padding-top: 25px;

            &>a {
                display: block;
                float: left;
                color: $headerAC;
                font-size: $smallfns2;
                padding-left: 15px;
                padding-top: 5px;

                img {
                    width: 30px;
                    height: 30px;
                }
            }

            &>a:nth-child(1) {
                padding-left: 0px;
            }

            &>a:nth-child(4) {
                padding-left: 30px;
                padding-top: 0px;
            }

            &>a:nth-child(5) {
                padding: 0 30px;
                position: relative;

                &>p.cart_count {
                    font-size: $realsmallfns;
                    position: absolute;
                    top: -6px;
                    right: 25px;
                }
            }
        }
    }

    .lnb {
        width: 1245px;
        height: 50px;
        margin: 0 auto;

        &>div {
            float: left;
            padding-top: 10px;
            font-size: $subtitle;
            font-weight: bold;
            margin-right: 30px;

            &>a {
                color: $headerAC;
            }
        }

        &>div:nth-child(2) {
            margin-right: 33px;
        }

        &>div:nth-child(3) {
            margin-right: 32px;
        }

        &>div:nth-child(4) {
            margin-right: 68px;
        }

        &>div:nth-child(5) {
            margin-right: 25px;
        }

        &>div:nth-child(6) {
            margin-right: 35px;
        }

        &>div:nth-child(7) {
            margin-right: 320px;
        }
    }

    .lnb_menu {
        width: 100%;
        // height: 400px;
        height: 0px;
        overflow: hidden;
        position: absolute;
        left: 0;
        top: 125px;
        background-color: white;
        z-index: 50;
        // border-bottom: 1px solid #ccc;
        border-bottom: 1px solid black;
        padding-left: 144px;
        box-sizing: border-box;
        transition: 0.3s;

        &>.lnb_menu_in {
            width: 1245px;
            height: 0px;
            // height: 400px;
            margin: 0 auto;
            position: relative;

            &>div {
                float: left;
                margin-top: 15px;
                margin-right: 30px;
            }

            &>div:first-child {
                margin-right: 50px;
            }

            &>div:nth-child(2) {
                margin-right: 45px;
            }

            &>div:nth-child(3) {
                margin-right: 70px;
            }

            &>div:nth-child(4) {
                margin-right: 55px;
            }

            &>div:nth-child(5) {
                margin-right: 35px;
            }


            &>div>a {
                display: block;
                color: black;
                margin-bottom: 15px;
                text-align: center;
            }

            .lnb_photoarea {
                position: absolute;
                right: 15px;
                bottom: 15px;
                overflow: hidden;

                img {
                    float: left;
                    width: 350px;
                    height: 200px;

                    &:last-child {
                        margin-left: 15px;
                    }
                }
            }
        }
    }
}